import React from 'react'
import './Cursor.css';

const Cursor = () => {

    const mouseskew = () => {
        var xprev = 0;
        var yprev = 0;


        window.addEventListener('mousemove', (data) => {
            var xdiff = data.clientX - xprev;
            var ydiff = data.clientY - yprev;
            // console.log(data.clientX, data.clientY);
           
            mouseMove(xdiff, ydiff)
        })

    }
    mouseskew();

    const mouseMove = (xdiff, ydiff) => {
        var timeout;
        window.addEventListener('mousemove', (data) => {
            clearTimeout(timeout);
            document.querySelector('#cursor').style = ` display : block`
            document.querySelector('#cursor').style.transform = `translate(${data.clientX - 5}px,${data.clientY - 5}px) scale(clamp(0.8, ${ydiff}/2, 1.2), clamp(0.8,  ${xdiff}/2, 1.2))`
            timeout = setTimeout(() => {
                document.querySelector('#cursor').style.transform = `translate(${data.clientX - 5}px,${data.clientY - 5}px) scale(1,1)`
            }, 100);
        })
    }

    return (
        <>
            <div id='cursor' className="cursor"></div>
        </>
    )
}

export default Cursor